<template>
  <section class="container-fluid" id="perfil">
    <div class="container">
      <div class="row">
        <div class="col-12">
          <h2 class="m-0"
              v-if="store.auth.usuario === null">{{ store.auth.email }}</h2>
          <h2 class="m-0"
              v-else>{{ store.auth.usuario }}</h2>
          <p>Detalles del Perfil</p>
        </div>

        <PerfilDatos
            v-if="editarUser"
            @click="editarUsuario()"/>
        <PerfilEditar
            v-else
            @click="editarUsuario()"/>

        <div class="col-12 col-md col-lg-4 posteos">
          <div class="row">
            <div class="col-12">
              <h2>¡Atenti!</h2>
            </div>
            <div class="col-12 px-1">
              <div class="posteo">
                <h3>Próximamente</h3>
                <p>Podrás ver tus posteos aquí! 😀... </p>
                <hr>
                <ul>
                  <li><span class="badge badge-pill badge-primary">Acá iría la fecha</span></li>
                  <li>
                    <router-link class="fav" to=""></router-link>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
      </div>
  </section>
</template>

<script>
import store from "@/store";
import UsersService from "@/services/users";
import PerfilEditar from "@/components/PerfilEditar";
import PerfilDatos from "@/components/PerfilDatos";

export default {
  name: "Perfil",
  components: {PerfilDatos, PerfilEditar },
  data: function () {
    return {
      store,
      editarUser: true
      ,
    }
  },
  methods:{
    editarUsuario: function () {
      if(this.editarUser){
        this.editarUser = false;
      }else {
        this.editarUser = true;
      }
    },
  },
  mounted() {
    const id = store.auth.id;
    UsersService
        .getByPk(id)
        .then(data => {
          this.usuario = data;
        });
  }
}
</script>

