<template>
  <div id="app">
    <header>
      <nav class="navbar navbar-expand-lg navbar-light">
        <router-link class="navbar-brand" to="/">
          <h1 class="logo">AddcarApp El Foro</h1>
        </router-link>
        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup"
                aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
          <div class="navbar-nav">
            <ul class="navbar-nav">
              <li class="nav-item">
                <router-link
                    class="nav-link"
                    data-ancla="true"
                    to="/">Inicio
                </router-link>
              </li>
              <li class="nav-item"
                  v-if="store.auth.id !== null">
                <router-link
                    class="nav-link"
                    data-ancla="true"
                    to="/perfil">Perfil
                </router-link>
              </li>
              <li class="nav-item"
                  v-if="store.auth.id === null">
                <router-link
                    class="nav-link"
                    to="/login">Iniciar Sesión
                </router-link>
              </li>
              <li class="nav-item"
                  v-if="store.auth.id === null">
                <router-link
                    class="nav-link"
                    to="/registro">Registrarme
                </router-link>
              </li>
              <li class="nav-item"
                  v-else>
                <button
                    type="button" class="btn btn-link"
                    @click="logout">{{ store.auth.email }} (Cerrar Sesión)
                </button>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </header>
    <BaseNotificacion
        v-if="store.status.mensaje != null"
        :titulo="store.status.titulo"
        :mensaje="store.status.mensaje"
        :tipo="store.status.tipo"
        @close="closeNotification()"
    />
    <router-view/>
  </div>
</template>

<script>
import store from './store';
import BaseNotificacion from './components/BaseNotificacion';
import authService from "./services/auth";

export default {
  name: 'App',
  components: {
    BaseNotificacion,
  },
  data: function () {
    return {
      store
    };
  },
  mounted: function () {
    if (localStorage.getItem('user') !== null) {
      this.store.auth = JSON.parse(localStorage.getItem('user'));
      authService.logUser(this.store.auth);
    }
  },
  methods: {
    closeNotification() {
      this.store.clearStatus();
    },

    logout() {
      authService
          .logout()
          .then(res => {
            // Deslogueamos al usuario.
            this.store.auth = {
              id: null,
              email: null,
              usuario: null,
              imagen: null,
            };

            this.estaCargando = false;
            // Lo borro de localStorage.
            localStorage.removeItem('user');
            this.store.clearStatus();
            this.$router.push('/login');
          });
    }
  }
}
</script>