<template>
  <section class="container-fluid" id="home">
    <div class="container">
      <div class="row">
        <div class="col-12 col-md col-lg posteos">
          <h2>Posteos Frecuentes</h2>

          <div v-if="store.auth.id !== null"
               class="nuevoPost">
            <router-link to="/PosteoNuevo">
                Crear nuevo tema
            </router-link>
          </div>

          <SpinnerLoader
              v-if="estaCargando"
          />
          <ListadoPosteos
              v-else
              :posteos="posteosOrdenados"
              @delete="refrescarPosteos()"
          />
        </div>
      </div>

    </div>
  </section>
</template>

<script>
import store from '../store';
import ListadoPosteos from "@/components/ListadoPosteos";
import SpinnerLoader from "@/components/SpinnerLoader";
import posteosService from "@/services/posteos";

export default {
  name: 'Home',
  components: {
    SpinnerLoader,
    ListadoPosteos,
  },
  data: function () {
    return {
      store,
      estaCargando: true,
      posteos: []
    }
  },
  computed: {
    posteosOrdenados: function () {
      const posteos = this.posteos.slice();
      posteos.reverse();
      return posteos;
    }
  },
  methods: {
    refrescarPosteos() {
      this.estaCargando = true;
      posteosService
          .getAll()
          .then(response => {
            this.estaCargando = false;
            this.posteos = response;
            console.log(response);
          });
    }
  },
  mounted: function () {
    this.estaCargando = true;
    posteosService
        .getAll()
        .then(response => {
          this.estaCargando = false;
          this.posteos = response;
        });
  }
}
</script>