<template>
  <div class="col-12 col-md-12 col-lg">
    <div class="row  rounded border p-3 mr-lg-4 bg-light mb-5 mb-md-5">
      <div class="col-6 col-md-4 col-lg-4 bg-light">
        <img class="img-fluid rounded"
             :src="imagenPath"
             :alt="usuario.email">
      </div>
      <div class="col-12 mt-3 md-md-0 col-md-8 col-lg bg-light">
        <h3 class="bg-light">Tus datos personales</h3>
        <p class="bg-light">Correo Electrónico: <span>{{ usuario.email }}</span></p>
        <p class="bg-light">Nombre de usuario:
          <span v-if="usuario.usuario === null" class="text-danger">
                  <i><u>
                      <span
                          @click="$emit('click', editarUser)">¡Agrega Uno!</span>
                  </u></i>
                </span>
          <span v-else>{{ usuario.usuario }}</span></p>
        <hr>
        <button
            type="button"
            class="btn btn-primary float-right"
            @click="$emit('click', editarUser)"
        >Edita tu perfil
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import store from "@/store";
import UsersService from "@/services/users";
import {PATH_IMGS} from "@/constants";

export default {
name: "PerfilDatos",
data: function () {
  return {
    usuario: {
      id: null,
      usuario: null,
      email: null,
      imagen: null,
      password: null
    },
    editarUser: false,
  }
},
  computed: {
    /**
     * @return {String}
     */
    imagenPath() {
      return `${PATH_IMGS}/perfil/${store.auth.imagen}`;
    },

    /**
     *
     * @return {boolean}
     */
    tieneImagen() {
      return store.auth.imagen !== null && store.auth.imagen !== '';
    }
  },
  mounted() {
    const id = store.auth.id;
    UsersService
        .getByPk(id)
        .then(data => {
          this.usuario = data;
        });
  }

}



</script>

