<template>
  <div class="col-12 col-lg-6 col-xl-3 px-1">
    <div class="posteo">
      <h3>
        <router-link
            :to="`/posteos/${posteo.id_posteo}`">{{ posteo.titulo }}
        </router-link>
      </h3>
      <p>creado por: <br> <i>{{ posteo.email }}</i></p>
      <hr>
      <ul>
        <li><span class="badge badge-pill badge-primary">{{ posteo.fecha }}</span></li>
        <li v-if="store.auth.id !== null ">
          <router-link
              class="fav"
              to=""></router-link>
        </li>
      </ul>
    </div>
  </div>

</template>

<script>
import store from "@/store";
import posteosService from "@/services/posteos";

export default {
  name: "PosteosMiniatura",
  props: ['posteo'],
  data: function() {
    return {
      store
    }
  },
  methods: {
    eliminar(id) {
      posteosService
          .deleteItem(id)
          .then(success => {
            if (success) {
              this.store.setStatus({
                titulo: 'Éxito',
                mensaje: 'El producto se eliminó exitosamente.',
                tipo: 'success'
              });
              this.$emit('delete');
            } else {
              this.store.setStatus({
                titulo: 'Error',
                mensaje: 'Ocurrió un error. El posteo NO se pudo eliminar.',
                tipo: 'danger'
              });
            }
          });
    }
  }
}
</script>

