<template>
  <div class="col-12 posteos">
    <div class="row">
      <div class="col-12 col-md-3 col-lg-3 user">
        <div class="row">
          <div class="col-4 col-md-12 col-lg-12">
            <img class="img-fluid rounded"
                 :src="imagenPath"
                 :alt="posteo.email">
          </div>
          <div class="col col-md col-lg">
            <small>Creado por:</small>
            <h2>{{ posteo.email }}</h2>
            <small>Fecha de creación: {{ posteo.fecha }}</small>
          </div>
        </div>
      </div>
      <div class="col-12 col-md col-lg tema">
        <h2 class="titulo">{{ posteo.titulo }}</h2>
        <hr>
        <p class="cuerpo">{{ posteo.cuerpo }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import {PATH_IMGS} from "@/constants";
import posteosService from "@/services/posteos";
import store from "@/store";

export default {
  name: "Posteo",
  props: {},
  data: function () {
    return {
      store,
      posteo: {
        id_posteo: null,
        titulo: null,
        cuerpo: null,
        fecha: null,
        id_usuario: null,
        email: null,
        userImg: null
      },
    }
  },
  computed: {
    /**
     * @return {String}
     */
    imagenPath() {
      return `${PATH_IMGS}/perfil/${this.posteo.userImg}`;
    },

    /**
     *
     * @return {boolean}
     */
    tieneImagen() {
      return this.posteo.userImg !== null && this.posteo.userImg !== '';
    }
  },
  methods: {},
  mounted() {
    const id = this.$route.params.id;
    posteosService
        .getByPk(id)
        .then(data => {
          this.posteo = data;
        });
  },
}
</script>